*{
    box-sizing: border-box;
}
.topbar-container{
    display: flex;
    justify-content:space-between;
    align-items: center;
    position: fixed;
    margin: 1vw;
    width: 98%;
    z-index: 100;
}
.logo{
    width:5vw;
    height:5vw;
}
.topbar-navs{
    padding: 1vw;
    font-size: max(1.5vw,20px);
    background-color: #07072F;
    border-radius: 10px;
    display: flex;
    align-items: center;
}
.nav-btn{
    color: #E4B6A5;
    text-decoration: none;
    margin-left: 2vw;
}
.nav-toggle{
    position: relative;
}
.toggle-btn{
    margin-top: 4px;
    display: none;
    position: absolute;
    right: 2%;
    top: 0;
    z-index: 101;
    font-size: 8vw;
    background-color: transparent;
    border: none;
    color: black;
}
.toggle-btn:hover{
    color: #E4B6A5;
}
.closed{
    color: #E4B6A5;
}
.closed:hover{
    color: white;
}
@media screen and (max-width:700px) {
    *{
        box-sizing: border-box;
    }
    .topbar-container{
        width: 100%;
        align-items: flex-start;
        padding: 0.10vw;
        padding-right: 0;
        margin-right: 0;
        margin-top: 0.6vh;
    }
    .logo{
        width:6vw;
        height:6vw;
        margin: 0;
    }
    .topbar-navs{
        width: 30%;
        background-color: transparent;
        margin-right: 0;
        padding-right:0;
        transition: all 0.75s ease-in-out;
        display:flex;
        flex-direction: column;
        align-items: center;
        font-weight: 450;
        border-radius: 0;
    }
    .nav-toggle{
        height: 5vh;
    }
    .nav-btns{
        width: 100%;
        background-color: rgb(255, 255, 255);
        display: flex;
        flex-direction: column;
        padding-left: 0;
        height: 30vh;
        padding-top: 0;
        justify-content: space-evenly;
    }
    .nav-btn{
        color: black;
        font-size: 3vw;
    }
    .toggle-btn{
        display: flex;
        padding: 0;
        height: 6vw;
        width: 6vw;
    }
    .hide{
        display: none;
    }
}